multiDefault()

const multiDefaults = () => {
	const elements = document.querySelectorAll('.multi-defaults');

	if (elements.length > 0) {
		elements.forEach(el => {
			// ,    Choices   
			if (!el.choices) {
				const choices = new Choices(el, {
					searchEnabled: false,
					allowHTML: true,
				});

				//  customInput  ID
				const customInput = document.getElementById('customInput');

				el.addEventListener('change', function () {
					if (customInput) { //   customInput
						if (el.value === 'other') {
							customInput.style.display = 'block';
							customInput.focus();
						} else {
							customInput.style.display = 'none';
						}
					}
				});

				if (customInput) { //   customInput   
					customInput.addEventListener('blur', function () {
						const customValue = customInput.value.trim();

						if (customValue) {
							const existingOption = Array.from(el.options).some(option => option.value === customValue);

							if (!existingOption) {
								const newOption = document.createElement('option');
								newOption.value = customValue;
								newOption.textContent = customValue;
								newOption.selected = true;

								el.appendChild(newOption);

								//  Choices    
								choices.setChoices(
									Array.from(el.options).map(option => ({
										value: option.value,
										label: option.textContent,
										selected: option.selected,
										disabled: option.disabled
									})),
									'value',
									'label',
									false //  false   Choices  
								);

								//     
								choices.setChoiceByValue(customValue);
							}

							customInput.value = '';
							customInput.style.display = 'none';
						}
					});
				}
			}
		});
	}
};

multiDefaults();

const select = document.getElementById('customSelect');
const customInput = document.getElementById('customInput');
let choicesInstance = new Choices(select); //   Choices  select

//   blur   
if (customInput) {
	customInput.addEventListener('blur', function () {
		const customValue = customInput.value.trim();

		if (customValue) {
			// ,     
			const existingOption = Array.from(select.options).some(option => option.value === customValue);

			if (!existingOption) {
				const newOption = document.createElement('option');
				newOption.value = customValue;
				newOption.textContent = customValue;
				newOption.selected = true;

				select.appendChild(newOption);

				//  Choices    
				choicesInstance.setChoices(
					Array.from(select.options).map(option => ({
						value: option.value,
						label: option.textContent,
						selected: option.selected,
						disabled: option.disabled
					})),
					'value',
					'label',
					false //  false   Choices  
				);

				//     
				choicesInstance.setChoiceByValue(customValue);
			}

			customInput.value = ''; //   
			customInput.style.display = 'none'; //     
		}
	});
}